@import url(https://fonts.googleapis.com/css?family=Cabin+Condensed|Cinzel|Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cabin+Condensed|Cinzel|Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cabin+Condensed|Cinzel|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}


.main-header {
  margin-top: 1.5%;
  margin-bottom: 0.5%;
  font-size: 3rem;
  font-family: 'Cinzel', serif;
  text-align: center;
}
/* Resets for Default Styling */

button, input[type="submit"] {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

/* Form Container */

.form-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.5%;
  align-items: center;
}

/* Toggle Button */

#toggle-form {
  margin-bottom: 1%;
  padding: 0.5% 1%;
  border: 1px solid black;
  border-radius: 5px;
  font-family: 'Cabin Condensed', sans-serif;
  color: white;
  background-color: navy;
}

/* Book Form */

.form-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#book-form {
  width: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1%;
  padding-bottom: 0.5%;
  border: 1px solid black;
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  text-align: center;
  background-color: lightgray;
}

#book-form input {
  margin-top: 1%;
  margin-bottom: 5%;
  border: 1px solid black;
}

#book-form .radio-btns {
  width: 100%;
  margin-top: 2%;
}

#book-form .read-radio-text {
  margin-right: 5%;
}

#book-form #submit {
  margin: 2% 0;
  padding: 1.5% 10%;
  border-radius: 5px;
  font-family: 'Cabin Condensed', sans-serif;
  background-color: #66ff66;
}

/* Form Toggling */

.hide {
  display: none;
}
/* Resets for Default Styling */

button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

/* Book List */

.book-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}

/* List Header */

.list-header {
  width: 75%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  border: 1px solid black;
}

.list-header div {
  min-width: 1%;
  flex-basis: 12.5%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5% 0;
  text-align: center;
  word-break: break-word;
  word-wrap: break-word;
  color: white;
  background-color: #290066;
}

.list-header .header-title, .list-header .header-author {
  flex-basis: 25%;
}

/* Book Entries */

.book {
  width: 75%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-left: 1px solid black;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
}

.book div {
  min-width: 1%;
  flex-basis: 12.5%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5% 0;
  text-align: center;
  word-break: break-word;
  word-wrap: break-word;
  background-color: #f0fff0;
}

.book .title, .book .author {
  flex-basis: 25%;
}

.book div button {
  padding: 3.5% 10%;
  border: 1px solid black;
  border-radius: 5px;
  font-family: 'Cabin Condensed', sans-serif;
}

.book .update-btn button {
  background-color: #80b3ff;
}

.book .delete-btn button {
  background-color: #ff4d4d;
}
